.homebgcss{
    height: 28rem;
    /* border: 2px solid red; */
    justify-content: center;
    text-align: center;
    position: -webkit-sticky;
 }
 .maincontent {
    justify-content: center;
    text-align: center;
 }
 .maintext{
    padding: 0 50px;
    width: 100%;
    display: block;
    font-size:2em;
    padding-top: 30px;
    color: rgb(0, 0, 0);
    text-align: center;
    justify-content: center;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
} 
.homebgcss::before {

        content: "";
        background: url('https://img.freepik.com/free-photo/mental-health-care-sketch-diagram_53876-122822.jpg?w=1380&t=st=1687836387~exp=1687836987~hmac=80f9d650521d6509a8770801acbd7389504ebfb199b272789fa8207feb477b78') no-repeat center center/cover;
        text-align: center;
        justify-content: center;
        position: absolute;
        left: 0;
        height: 28rem;
        width: 100%;
        z-index: -2;
        opacity: 0.7;
}
@media only screen and (max-device-width: 700px) {
    .homebgcss {
        height: 36rem;
    }
    .homebgcss::before {
        height: 65%;
    }
    .maintext{
        font-size:1.8em;
    }
  }
.mainbutton{
    border:none;
    text-align: center;
    justify-content: center;
    font-size: 1.2em;
    border-radius: 10px;
    background-color: rgb(255, 255, 74);
    color: black;
    display: block;
    margin: 1em auto;
    width: 7em;
}
.mainbutton:hover{
    color: white;
    background-color: black;
    border-radius: 10px;
}
.appoinment{
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-image: url("https://img.freepik.com/free-vector/geometric-background_53876-115958.jpg?w=1380&t=st=1687837582~exp=1687838182~hmac=2068b5b13d969ed7f1bde5aac89e93aa1c504b5592da64debd350cfcf82e1415") ;
}

.bookheading{
    width: 100%;
    margin-top: 0em;
    text-align: center;
    font-size: 3rem;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    /* background-color: rgb(37, 37, 37); */
    /* border-radius: 2em; */
}
.step{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.steps{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    margin: 1em;
}
.paraofstep{
    width: 30rem;
    margin: 0 2em;
    padding: 1em 1em;
}
.steptext{
    padding: 1em;
    font-size: 1.3rem;
    margin: 1em 1em;
    text-align: center;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.imgstep1{
    width: 10em;
    height:10em;
    margin: 1em auto;
    border-radius: 1em;
}
.imgstep3{
    width: 10em;
    height:10em;
    margin: 0em auto;
    border-radius: 1em;
}
#step-2para{
  flex-wrap: wrap-reverse;
}
.stepheading{
    width: 5em;
    display: blocks;
    margin: 0 auto;
    text-align: center;
    background-color: rgb(181, 99, 99);
    border-radius: 2em;
    font-size: 1.5rem;
}